*{
  box-sizing: border-box;
}

body, html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.jumbotron.home {
  background-image: url('https://i.redd.it/jks7bjesfbb21.jpg');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 50vh;
  position: sticky;
}

.jumbotron.about {
  background-image: url('./ditto.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 50vh;
  position: sticky;
}

.banner-text {
  background-color: #ffffffa6;
}

.jumbotron.search {
  background-image: url('./applogo-banner_orig.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  height: 20vh;
  position: sticky;
}

.container#poke-body {
  max-width: 1340px
}

#title-row, #poke-title {
  width: 100%;
  margin: 0 auto;
}

#poke-title a{
  color: black;
}

#poke-title a:hover{
  color: #af1e1e;
  text-decoration: none;
}

.section {
  background-color: #ffffffd5;
  border-radius: 10px;
}

#poke-desc-body {
  display: block;
  height: 304px;
  overflow-y: auto;
}

#poke-desc-body, #poke-desc-body tr{
  width: 100%
}

#poke-desc-table {
  margin-top: -17px
}

.evo-caption {
  margin-top: -20px;
  padding-bottom: 10px;
}

#evo-container {
  margin-top: -10px;
}

#slider {
  -webkit-appearance: none;
  width: 100%;
  height: 25px;
  background: #d3d3d3;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

#slider:hover {
  opacity: 1;
}

#slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  background: #4CAF50;
  cursor: pointer;
}

#slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  background: #4CAF50;
  cursor: pointer;
}

.progress.stat-bar {
  background-color: transparent;
}
.v-low .progress-bar{
  background-color: #f34444;
  border: 1px solid #cf3a3a;
  border-radius: 5px;
  height: 12px;
}
.low .progress-bar{
  background-color: #ff7f0f;
  border: 1px solid #d96c0d;
  border-radius: 5px;
  height: 12px;
}
.medium .progress-bar{
  background-color: #ffdd57;
  border: 1px solid #d9bc4a;
  border-radius: 5px;
  height: 12px;
}
.high .progress-bar{
  background-color: #a0e515;
  border: 1px solid #88c312;
  border-radius: 5px;
  height: 12px;
}
.v-high .progress-bar{
  background-color: #23cd5e;
  border: 1px solid #1eae50;
  border-radius: 5px;
  height: 12px;
}
.max .progress-bar{
  background-color: #00c2b8;
  border: 1px solid #00a59d;
  border-radius: 5px;
  height: 12px;
}

